/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: rgba(0, 0, 0, 0.856);
  color: #333;
}
nav h1{
  padding-left: 30px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
nav h1 span{
 color:#c88081
}
header, footer {
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 10px 0;
}

header h1, footer {
  margin: 0;
}
nav{
  display:flex;
  justify-content: space-between;
  align-items: center;
  
}
nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
 
}

nav ul li {
  margin: 0 10px;
  font-family:'Courier New', Courier, monospace;
  padding-inline-start: 10px;
  padding-inline-end: 10px;
 
}

nav ul li a {
  color: white;
  text-decoration: none;
   font-weight: 200;
}


 nav ul li a:focus {
  border:1px solid rgba(255, 255, 255, 0.37);
  padding:6px;
  border-radius: 10px;
 }



/* Registration Form */
form {
  display: flex;
  flex-direction: column;
}

form label {
  margin: 10px 0 5px;
}

form input, form textarea, form select {
  padding: 8px;
  border: 1px solid #0c0c0c9c;
  border-radius: 4px;
  font-size: 16px;
  background-color: rgba(0, 0, 0, 0.534);
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

form button {
  margin-top: 20px;
  padding: 10px;
  background-color: #0077cc;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

form button:hover {
  background-color: #005fa3;
}

/* Registration Details */
.details {
  padding: 20px;
  background: #fff;
  border: 1px solid #ddd;
  margin: 10px 0;
  border-radius: 8px;
}

.details p {
  margin: 0;
  padding: 5px 0;
}

.details button {
  margin-top: 10px;
  padding: 8px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.details button:hover {
  background-color: darkred;
}
